*{
    padding:0%;
    margin:0%;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}

body{
    min-height: 100vh;
    background-color: #23252C;
    padding: 30px;
    display:flex;
    justify-content: center;
}

.profile-pic{
    width:317px;
}

.main{
    width:317px;
    height:auto;
    background-color: #1A1B21;
    border-radius:10px;
    overflow: hidden;
}

.top-text{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px 30px;
}

.name{
    font-size:25px;
    font-weight:700;
     margin-bottom: 5px;
    color: #ffffff;
}

.job{
     margin-bottom: 5px;
    font-size:12.8px;
    font-weight:400;
    color: #F3BF99;
}

.website{
    color:#F5F5F5;
    font-size:10.5px;
    font-weight:400;
    margin-bottom:15px;
}

.buttons{
    display:flex;
    align-items: center;
    justify-content: center;
    gap:10px;
}

.buttons button{
    border:none;
    width: 115px;
    height:34px;
    font-size:14px;
    border-radius:6px
}

.linkedin{
    background-color: #5093E2;
    color: #ffffff
}

.bottom{
    padding: 20px 40px;
    color:#ffffff
}

.bottom-heading{
    font-size: 16px;
    font-weight:700;
    margin-bottom: 3px;
}

.bottom-p{
    color:#DCDCDC;
    font-size:10.24px;
    margin-bottom:20px;
}

.footer{
    height:64px;
    background-color:#161619;
    color:#918E9B;
    font-size:25px;
    display:flex;
    gap:25px;
    align-items: center;
    justify-content: center;
}